import Vue from 'vue'
import App from './App.vue'
import router from './router'
import { Menu, MenuItem, Submenu, Input, Button, Select, Option, Tooltip, InfiniteScroll} from 'element-ui'

Vue.config.productionTip = false

Vue.use(Menu).use(MenuItem).use(Submenu).use(Input).use(Button).use(Select).use(Option).use(Tooltip).use(InfiniteScroll)

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
