<template>
<div>
  <input type="text" disabled id="scrollLeftBox">
  <div class="btnwrap">
    <el-button type="success" plain>发帖</el-button>
  </div>
  <div class="msgwrap infinite-list" style="overflow:auto">
    <ul
      class="list"
      v-infinite-scroll="load"
      infinite-scroll-disabled="disabled">
      <li v-for="i in count" class="list-item" :key="i">{{ i }}</li>
    </ul>
    <p v-if="loading">加载中...</p>
    <p v-if="noMore">没有更多了</p>
  </div>


</div>
</template>

<script>
export default {
  name: 'forum',
  data() {
    return {
      count: 10,
      loading :false
    }
  },
  mounted(){
    this.init()
  },
  computed: {
    noMore () {
      return this.count >= 200
    },
    disabled () {
      return this.loading || this.noMore
    }
  },
  methods: {
    init(){
      let str = '1、交流区无需注册，可以自由留言；2、请自觉遵守网络文明公约，严禁攻击诋毁、低俗色情等内容；3、保持学术纯洁，莫谈国是；4、点击上面的“神秘花园”可能会有意外收获；5、欢迎发表对网站的意见建议；';
      let scrollLeftBox = document.getElementById('scrollLeftBox');               
      setInterval(()=>{
        scrollLeftBox.value = str;
        str = str.substring(1, str.length) +　str.substring(0, 1);
      }, 1000);
    },
    load () {
      this.loading = true
      setTimeout(() => {
        this.count += 2
        this.loading = false
      }, 2000)
    }
  }
}
</script>

